html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: monospace, sans-serif;
    line-height: 1.3;
    color: $color-text;
    background-color: $color-background;
}


.wrap {
    width: 1000px;
    max-width: 90%;
    margin: 0 auto;
}


.logo {
    height: 30px;
    position: relative;
    display: flex;
    align-items: center;

    span {
        position: relative;
        padding-left: 10px;
        font-weight: bold;
        font-size: 1.3rem;
    }
}


img {
    height: 100%;
}

a {
    text-decoration: none;
    color: $color-text;
    transition: all .2s;
    position: relative;
    display: inline-block;
}


a:not(.ignore) {
    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $color-accent;
        z-index: 5;
        transform: scaleX(0);
        transform-origin: left;
        transition: all .2s;
    }

    &:hover {
        color: $color-accent;
        
        &::after {
            transform: scaleX(1);
        }
    }
}


.overlay-trans {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    z-index: 99;
}


#flash {
    text-align: center;
    background-color: #F65B65;
    color: #fff;   
}


.toast {
    position: fixed;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
    padding: 10px 20px;
    border-radius: 10px;
}
