.script-list.single {
    .script {
        flex: 0 1 auto;
        max-width: 100%;
        width: 500px;
        min-width: auto;
    }
} 

.script {
    display: flex;
    flex-direction: column;
    padding:  20px;
    background-color: $color-background-light;
    flex: 1;
    border-radius: 10px;
    min-width: 350px;

    .top {
        justify-content: space-between;
    }

    hr {
        margin: 0;
        width: 100%;
        border: 0;
        border-top: 1px solid rgba($color: #fff, $alpha: 0.1);
    }

    .dropdown-btn {
        width: 40px;
        height: 40px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all .2s;
        position: relative;

        &:hover { background-color: $color-background; }
    }

    .dropdown-menu {
        position: absolute;
        top: 40px;
        right: 0;
        background-color: $color-background;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        z-index: 999;
        // width: 200px;
        padding: 10px 0;
        display: flex;
        flex-direction: column;
        display: none;

        a {
            padding: 10px 20px;
            width: 100%;
            white-space: nowrap;
            font-size: 1.2rem;
        }

        a:hover {
            background-color: rgba($color: #fff, $alpha: 0.1);
        }
    }

    .col {
        display: flex;
        flex-direction: column;
    }

    .row {
        display: flex;
        flex-direction: row;
    }

    .actions a {
        display: inline-flex;
        align-items: center;
        padding: 5px 10px;
        border-radius: 5px;
        background-color: $color-background;
    }

    .actions {
        gap: 20px;
        flex-wrap: wrap;
    }

    .delete {
        color: #ff0000;
    }

    .run {
        color: #00ff00;
    }

    .spacer {
        flex-grow: 1;
    }

    .info {
        padding-bottom: 20px;
    }

    .fileName {
        font-size: 1rem;
        color: rgba($color: $color-text, $alpha: 0.7);
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center;

        .material-icons {
            font-size: 0.9rem;
            padding-right: 5px;
        }
    }

    .title {
        font-size: 1.3rem;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}