.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    display: none;
}

.dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 100%;
    max-height: 100%;
    overflow: auto;
    transform: translate(-50%, -50%);
    background-color: rgba(60, 58, 56, 1);
    z-index: 99;
    padding: 20px 20px 10px 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    font-family: monospace, sans-serif;
    color: #eee;
    font-size: 1.2rem;
    display: none;
    width: 500px;
    max-width: 100%;

    .dialog-title {
        font-size: 1.3rem;
        margin-bottom: 10px;
    }

    .dialog-button-container {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;
    }
}

.dialog .btns {
    text-align: center;
    padding-top: 20px;
}

.dialog button {
    background-color: transparent;
    border: none;
    color: #66bb6a;
    font-family: monospace, sans-serif;
    font-size: 1.2rem;
    border-radius: 4px;
    padding: 10px 20px;
    transition: all .2s;
}

.dialog button:hover {
    background-color: rgba(255, 255, 255, 0.1);
    cursor: pointer;
}


.dialog.prompt {
    width: 300px;
    max-width: 100%;


    .dialog-input {
        width: 100%;
        padding: 10px 20px;
        border: none;
        background-color: $color-background;
        border-radius: 4px;
        color: #eee;
        font-size: 1.1rem;
        margin-bottom: 10px;
    }
}